import React from 'react';
import {Link,Route} from 'react-router-dom';
import { Helmet } from 'react-helmet';
// 
/* eslint-disable react/no-multi-comp */ 
const Snacks = () =>{
    const head = ()=>{
        return (
            <Helmet>
              <title></title>
              <meta property="og:title" content="" />
            </Helmet>
        );
    };
    return(
        <>
            {head()}
            <h2></h2>
            <ul>
                <li><Link to="/snacks/one"></Link></li>
                <li><Link to="/snacks/two"></Link></li>
            </ul>
            {/*   */}
            <div className="router_sub">
                <Route path="/snacks/one" component={()=><h2>1</h2>} />
                <Route path="/snacks/two" component={() => <h2>2</h2>} />
            </div>
        </>
    );
};
export default Snacks;